html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
p {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font-family: arial, 'Hiragino Sans GB', 'Microsoft Yahei', '微软雅黑', '宋体', \5b8b\4f53, Tahoma, Arial, Helvetica, STHeiti;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}
em {
  font-style: normal;
}
ul,
ol {
  list-style: none;
}
fieldset,
img {
  border: 0;
}
button,
input,
select,
textarea {
  font-size: 100%;
}
.banner-img {
  width: 100%;
  border-radius: 5px;
}
.recommend-content {
  padding: 2px 10px 0;
}
.recommend-title {
  font-weight: 500;
  line-height: 30px;
  font-size: 14px;
}
.recommend-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.recommend-list-item {
  flex: 1;
  align-items: center;
}
.recommend-list-item > span {
  padding: 2px;
  font-size: 12px;
  line-height: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  height: 26px;
  margin: 5px 0 10px;
}
.recommend-list-active {
  width: 100px;
  height: 100px;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}
.recommend-list-active > img {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.4s;
}
.recommend-list-active:hover > img {
  zoom: 1;
  transform: scale(1.5);
}
.list {
  height: 100%;
  overflow: auto;
}
.list-top {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  height: 250px;
  padding: 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.3);
}
.list-top-title {
  display: flex;
  align-items: center;
  font-size: 16px;
}
.list-top-bottom {
  margin-bottom: 15px;
}
.list-top-img {
  position: absolute;
  width: 100%;
  height: 250px;
  left: 0;
  top: 0;
  z-index: -1;
}
.list-container {
  position: relative;
  top: -10px;
  background: #f2f3f4;
  border-radius: 10px 10px 0 0;
}
.list-content {
  background: #f2f3f4;
  font-size: 14px;
  padding-bottom: 60px;
}
.list-content-top {
  padding: 10px;
  border-bottom: 1px solid #e4e4e4;
}
.list-content-item {
  display: flex;
  align-items: center;
  padding: 10px 15px;
}
.list-content-border {
  border-bottom: 1px solid #e4e4e4;
}
.rank {
  padding: 10px;
  background: #f2f3f4;
}
.rank-item {
  padding: 5px 0;
}
.rank-item-img {
  width: 100px;
  height: 100px;
  border-radius: 3px;
}
.rank-item-right {
  padding: 10px;
  justify-content: space-around;
}
.rank-item-text {
  display: inline-block;
  width: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 12px;
}
.border {
  border-bottom: 1px solid #e4e4e4;
}
